.formBlockContainer {
    display: flex;
    justify-content: center;
    align-items: center;    
}

.formBlock {
    display: flex;
    align-items: center;
    width: 100%;
    background-color: white;
    flex-direction: column;
}

.title {
    font-size: 38px;
    font-weight: 200;
    margin-top: 10px;
    margin-bottom: 10px;
}

.formApplication {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    width: 100%;
    background-color: rgba(255, 255, 255, .5);
    padding: 20px 0;
}

.formApplication span {
    margin-bottom: 10px;
    font-size: 30px;
}

.InputMain {
    color: black;
    border: none;
    outline: none;
    font-size: 15px;
    width: 450px;
    height: 100%;
    margin: 8px 0;
    padding: 13px;
    box-sizing: border-box;
    line-height: 20px;
    border-bottom: 2px solid rgb(95, 73, 185);
    background-color: rgba(123, 110, 120, 0.36);
}

.InputPhoto {
    width: 100%;
    margin-top: 20px;
    display: inherit;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.InputPhoto span {
    font-size: 20px;
    margin-bottom: 0;
}

.InputPhoto input {
    margin: 0;
}

.button {
    margin: 15px 0;
    padding: 7px;
    font-size: 20px;
    color: white;
    border: none;
    border-radius: 5px;
    background-color: #b582f8;
    cursor: pointer;
    text-decoration: none;
    --color1: purple;
    --color2: #874099;
    animation: hoverAnimButtonOut .5s forwards;
}

.button:hover {
    --color1: purple;
    --color2: #874099;

    animation: hoverAnimButtonIn .5s forwards;
}

#statusSpan {
    font-size: 20px;
}

@media only screen and (max-width: 1000px) {
    .InputMain {
        width: 90%;
    }
}