.converterContainer {
    background-color: #f2f2f7;
    height: 100px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.converterContainer i {
    font-size: 30px;
    cursor: pointer;
}

.converterContainer input, select, i {
    margin: 0 5px;
}

input[type=number] {
    appearance: textfield;
}

.converterContainer .btn-convert {
    margin-top: 10px;
    padding: 10px;
    width: 100px;
    font-weight: 300;
    font-size: 15px;
    color: white;
    border: none;
    border-radius: 5px;
    background-color: #b582f8;
    cursor: pointer;
    text-decoration: none;

    --color1: purple;
    --color2: #874099;

    animation: hoverAnimButtonOut .5s forwards;
}

.converter-main {
    justify-content: center;
    display: flex;
    align-items: center;
}

.ban-overlay {
    width: 100%;
    height: 100px;
    position: absolute;
    background: rgba(0, 0, 0, .8);
    display: flex;
    justify-content: center;
    align-items: center;
}

.ban-overlay #ban-logo::before {
    opacity: 0;
    animation: out 1s forwards;
}

.ban-overlay:hover #ban-logo::before  {
    opacity: 1;
    animation: in .5s forwards;
}

@keyframes out {
    to {
        opacity: 1
    }
}

@keyframes in {
    50% {
        opacity: 0;
        font-size: 70px;
        font-weight: 400;
        line-height: 0px;
        content: "";
    }
    
    51% {
        opacity: 0;
        font-weight: 400;
        font-family: 'Roboto', sans-serif;
        line-height: 100px; 
        font-size: 20px;
    }
    
    100% {
        opacity: 1;
        font-weight: 400;
        font-family: 'Roboto', sans-serif;
        line-height: 100px; 
        font-size: 20px;
        content: "To use converter, please register on this site.";
    }
}

.Table {
    width: 100%;
    display: flex;
    justify-content: center;
}

table {
    width: 100%;
    margin-top: 30px;
    margin-bottom: 30px;
    border-collapse: collapse;
}

th {
    padding: 10px;
    background-color: #f0fbffc2;
    font-weight: 200;
    border: 1px solid #253443;;
}

.TableHeaderCurrencyCode {
    font-weight: 700;
    width: 100px;
}

.TableHeaderCurrencyName {
    width: 238px;
    font-weight: 700;
}

.TableHeaderCurrencyRate {
    width: 238px;
    font-weight: 700;
}

@media only screen and (max-width: 1000px) {
    .converterContainer, .ban-overlay {
        height: 140px;
    }

    table{
        margin-right: 20px;
    }

    th {
        padding: 10px 0;
    }

    .converter-main{
        flex-direction: column;
    }
    
    .converterContainer button {
        margin-top: 10px;
    }

    @keyframes in {
        50% {
            opacity: 0;
            font-size: 70px;
            font-weight: 400;
            line-height: 0px;
            content: "";
        }
        
        51% {
            opacity: 0;
            font-weight: 400;
            font-family: 'Roboto', sans-serif;
            line-height: 100px; 
            font-size: 15px;
        }
        
        100% {
            opacity: 1;
            font-weight: 400;
            font-family: 'Roboto', sans-serif;
            line-height: 100px; 
            font-size: 15px;
            content: "To use converter, please register on this site.";
        }
    }
}