body {
    background-color: #d2f4ffeb;
    margin: 0;
    padding-bottom: 80px;
    font-family: Roboto;
    font-weight: lighter;
}

.headerContainer {
    background-color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 60px;
    width: 100%;
    margin-bottom: 20px;
}

.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 640px;
    height: 100%;
}

.LogoTitle {
    font-size: 30px;

    text-decoration: none;
    color: black;

    animation: hoverAnimTextOut .5s forwards;
    --color1: black;
    --color2: gray;
}

.LogoTitle:hover {
    animation: hoverAnimTextIn .5s forwards;
    --color1: black;
    --color2: gray;
}

.userInfo {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    width: 200px;
    font-size: 24px;
}

.userInfo img {
    border-radius: 20px;
}

.userInfo span {
    margin: 0 5px;
}

.userInfo a {
    margin-right: 5px;
    text-decoration: none;
    color: black;
}

.btn-login {
    padding: 7px;
    font-weight: 300;
    font-size: 20px;
    color: white;
    border: none;
    border-radius: 5px;
    background-color: #b582f8;
    cursor: pointer;
    text-decoration: none;

    --color1: purple;
    --color2: #874099;

    animation: hoverAnimButtonOut .5s forwards;
}

.btn-login:hover {
    --color1: purple;
    --color2: #874099;

    animation: hoverAnimButtonIn .5s forwards;
}

.userInfo .btn-logout {
    padding: 7px;
    font-weight: 300;
    font-size: 20px;
    color: white;
    border: none;
    border-radius: 5px;
    background-color: red;
    cursor: pointer;
    text-decoration: none;

    --color1: purple;
    --color2: #874099;

    animation: hoverAnimButtonOut .5s forwards;
}

.userInfo .btn-logout:hover {
    --color1: purple;
    --color2: #874099;

    animation: hoverAnimButtonIn .5s forwards;
}

@keyframes hoverAnimTextIn {
    to {
        color: var(--color2, 0%);
    }
}

@keyframes hoverAnimTextOut {
    from {
        color: var(--color2, 0%);
    }

    to {
        color: var(--color1, 0%);
    }
}

@keyframes hoverAnimButtonIn {
    from {
        background-color: var(--color1, 0%);
    }

    to {
        background-color: var(--color2, 0%);
    }
}

@keyframes hoverAnimButtonOut {
    from {
        background-color: var(--color2, 0%);
    }

    to {
        background-color: var(--color1, 0%);
    }
}

.contentContainer {
    width: 100%;
    display: flex;
    justify-content: center;
}

.content {
    position: absolute;
    width: 640px;
}

@media only screen and (max-width: 1000px) {
    .LogoTitle {
        font-size: 20px;
    }

    .header, .content {
        width: 95%;
    }
}